import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { OmniPullService } from 'src/app/commonBussinessSRC/psServices/omni-common/omni-pull.service';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { IOptionsAlertButton, IOptionsPsActionIcon, IOptionsPsActionImage, IOptionsPsContainerPanel, IoptionsPsContainerWidgetCard, IOptionsPsLabel, IOptionsPsTemplateView } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { OmniBasePage } from '../omni-base/omni-base.page';
import { CommonUtils } from 'src/app/commonSRC/psServices/models/common-utils';
import { PsCommonService } from '../../commonSRC/psServices/ps-common/ps-common.service';
import { CdkDrag, CdkDragDrop, CdkDragMove, CdkDropList, CdkDropListGroup, moveItemInArray, transferArrayItem } from '@angular/cdk/drag-drop';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { IDashboardWidgets } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-interfaces';
import { ViewportRuler } from '@angular/cdk/overlay';
@Component({
  selector: 'dashboard-page',
  templateUrl: 'dashboard.page.html',
})
export class DashboardPage extends OmniBasePage implements OnInit {
  formGroup: FormGroup = new FormGroup({});
  @ViewChild(CdkDropListGroup) listGroup: CdkDropListGroup<CdkDropList>;
  @ViewChild('placeholder', { static: false, read: CdkDropList }) placeholder!: CdkDropList;
  @ViewChild('container') container!: ElementRef;
  autoScrollInterval: any;
  constructor(
    public commonService: PsCommonService, public omniPull: OmniPullService,private logger:LoggerService,private viewportRuler: ViewportRuler) {
    super();

  }
  public templateViewOptions: IOptionsPsTemplateView = {
    group: this.formGroup,
    disableLoading:true
  };  
  spinner = false;

  unAddedUserWidgets:IoptionsPsContainerWidgetCard[] = []; // will contain the unadded available widgets within the dashboard
  userWidgets:IoptionsPsContainerWidgetCard[] = []; // will contain the already added widgets within the dashboard
  currentLayout: 'columns' | 'rows' | 'grid' = 'rows'
  dropListOrientation:'horizontal' | 'vertical' = 'vertical'
  cdkDropListConnectedTo = ['userWidgets']
  cdkDragLockAxis: 'x' | 'y' = 'y'
  source: CdkDropList;
  activeContainer: CdkDropList<any>;
  targetIndex: number;
  sourceIndex: number;
  isMobile: boolean;
  showAvailableWidgets = false;

  unusedWidgetsPanelOpts: IOptionsPsContainerPanel = {
    isExpandable: true,
    labelKey: 'choose_your_widget_key',
    iconName: 'document',
    collapseIconName:'close',
    expanded: true
  };
  dropHereLabel:IOptionsPsLabel = {
    labelKey:'drop_widget_here_key',
  }
  addWidgetIconOptions: IOptionsPsActionIcon = {
    iconName: 'add',
    labelOptions:{
      labelKey:'Click_here_to_add_widgets_key'
    },
    allowCust: true
  };
  actionImageOptions: IOptionsPsActionImage = {
    imageName: CommonUtils.getCssVariableValue('--ps-loader-image-name')
  };
  layoutIconOptions:IOptionsPsActionIcon[] = [{
    iconName: 'rows-layout-icon',
    allowCust: true,
    handler:()=>{this.onLayoutChange('rows')}
  },
  {
    iconName: 'columns-layout-icon',
    allowCust: true,
    handler:()=>{this.onLayoutChange('columns')}
  },{
    iconName: 'grid-layout-icon',
    allowCust: true,
    handler:()=>{this.onLayoutChange('grid')}
  }]

  dragWidgetIcon: IOptionsPsActionIcon = {
    iconName: 'drag-icon',
    allowCust: true  
  };

 
  ngOnInit() {
    super.init();
    this.isMobile = this.commonProv.isMobile();
    let userInfo = this.commonProv.session.getValueOf(ConstantCommon.USERINFO);
    if(!this.isMobile){
     const dashboardLayout = userInfo?.omniUserVO && userInfo.omniUserVO.DASHBOARD_LAYOUT ? userInfo.omniUserVO.DASHBOARD_LAYOUT : this.currentLayout
     if(dashboardLayout !== this.currentLayout){
      this.changeCurrentLayout(dashboardLayout);
     }
    }
    this.returnWidgetDetails()
  }

  async returnWidgetDetails(){
    let fetchedWidgets = []
    this.spinner = true;
    const result = await this.omniPull.returnWidgetDetails().catch(error => {
      this.spinner = false;
      this.logger.error('Error: While fetching dashboard widgets in DashboardPage :', error);
    });  
    this.spinner = false 
    if (result && result.widgets && result.widgets.length > 0) {
      fetchedWidgets = this.populateDashbordWidgets(result.widgets);
    }
    const { userWidgets, unusedWidgets } = this.categorizeWidgets(fetchedWidgets);
    this.unAddedUserWidgets = unusedWidgets;
    this.userWidgets = this.sortWidgetsByOrder(userWidgets);
    if(this.currentLayout == 'grid'){
      this.cdkDropListConnectedTo = this.generateCdkDropListConnectedTo();
    }
  }

  populateDashbordWidgets(requestData:IDashboardWidgets[]){
    let fetchedWidgets:IoptionsPsContainerWidgetCard[] = [];
    for (const iterator of requestData) {
      const widget:IoptionsPsContainerWidgetCard = {
      widgetCode:iterator.widgetCode,
      widgetId:iterator.widgetId,
      widgetHeaderName:iterator.headerName,
      widgetTitle:iterator.label,
      widgetReportId:iterator.idReport,
      widgetReportType:iterator.type,
      linkedOperation:iterator.linkedOper,
      widgetIconName:this.getWidgetIconName(iterator.type),
      widgetOrder:iterator.widgetOrder,
    }
    fetchedWidgets.push(widget);
    } 
    return fetchedWidgets   
  }
 
  categorizeWidgets(widgets: IoptionsPsContainerWidgetCard[]) {
    return widgets.reduce(
      (result, widget) => {
        if (widget.widgetOrder !== undefined && widget.widgetOrder !== null) {
          result.userWidgets.push(widget);
        } else {
          result.unusedWidgets.push(widget);
        }
        return result;
      },
      { userWidgets: [], unusedWidgets: [] }
    );
  }

  sortWidgetsByOrder(userWidgets: IoptionsPsContainerWidgetCard[]): any[] {
    return userWidgets.sort((a, b) => a.widgetOrder - b.widgetOrder); 
  }

  removeWidgetConfirmationPopup(widgetCode: number) {
    let buttonsObject: Array<IOptionsAlertButton> = [
      {
       type: 'reset',
       group:this.formGroup,
       labelKey: 'cancel_key',
       handler: () => {
         CommonUtils.dismissAllModals();
       }
      },
      { 
       group:this.formGroup,
       labelKey: 'ok_key',
       type: 'submit',
       handler: () => {
         this.removeWidget(widgetCode);
         CommonUtils.dismissAllModals();
       }
      }
     ];
    CommonUtils.presentInfoAlert(this.commonProv.translate('are_you_sure_?_key'), { buttonsArray: buttonsObject });
  }
  removeWidget(widgetCode: number){
   if(widgetCode){
    let removedWidget = {}
    const widgetIndex = this.userWidgets.findIndex(widget => widget.widgetCode === widgetCode);
    if (widgetIndex !== -1) {
     removedWidget = this.userWidgets.splice(widgetIndex, 1);
    } 
    this.unAddedUserWidgets.push(removedWidget[0]);
    this.saveDashboardPersonalization();
   }
  }
 async saveDashboardPersonalization(layout?: string){
    return new Promise<any>((resolve, reject) => { 
    this.commonProv.presentLoading();
    const widgetsArray = this.assignWidgetsOrder(this.userWidgets); 
    let params = {
      widgetsArray,
      ...(widgetsArray.length === 0 && !layout && { isDashboardEmpty: 'Y' }),
      ...(layout && { dashboardLayout: layout, widgetsArray:[] }),
    };
    this.omniPull.saveDashboardPersonalization(params).then((result)=>{
     resolve(true)
  }).catch(error => {
     CommonUtils.presentFatalAlert(this.commonProv.translate('dashboard_saving_personalization_error'))
     this.logger.error('Error: While saving dashboard widgets personalization:', error);
     reject(false)
   }).finally(()=>{
     this.commonProv.dismissLoading();
   })
  })
  }

  assignWidgetsOrder(widgets: any[]): { widgetCode: number; widgetId: number; widgetOrder: number }[] {
    return widgets.map((widget, index) => ({
      widgetCode: widget.widgetCode,
      widgetId: widget.widgetId,
      widgetOrder: index,
    }));
  }

  onLayoutChange(layout: string){
    this.changeCurrentLayout(layout);
    setTimeout(() => {
      this.commonProv.chartResizeSubject.next(true)
    }, 0);
    this.saveDashboardPersonalization(layout).then((result)=>{
     if(result){
      this.updateLayoutFromSession(layout);
     }
    })
  }

  resizeWidgetChart(){
    setTimeout(() => {
      this.commonProv.chartResizeSubject.next(true)
    }, 0);
  }
  changeCurrentLayout(layout:string){
    switch(layout){
      case 'columns':
        this.cdkDragLockAxis = 'x';
        this.dropListOrientation ='horizontal';
        this.currentLayout = 'columns'  ;
        this.cdkDropListConnectedTo = ['userWidgets']
        break;
      case 'rows':
        this.cdkDragLockAxis = 'y';
        this.dropListOrientation ='vertical';
        this.currentLayout = 'rows';
        this.cdkDropListConnectedTo = ['userWidgets']
        break;
      case 'grid':
        this.currentLayout = 'grid';
        setTimeout(() => {
          this.removePlaceHolder();     
        }, 0);
        this.cdkDropListConnectedTo = this.generateCdkDropListConnectedTo();
        break;
      default:
        break;
    }
  }
  
  generateCdkDropListConnectedTo(): string[] {
    return this.userWidgets.length > 0 
      ? this.userWidgets.map((_, index) => `userWidgets-${index}`) 
      : ['userWidgets-0'];
  }

  updateLayoutFromSession(layout: string): void {
    const userInfo = this.commonProv.session.getValueOf(ConstantCommon.USERINFO);
    if (userInfo?.omniUserVO) {
      userInfo.omniUserVO.DASHBOARD_LAYOUT = layout;
      this.commonProv.session.append(ConstantCommon.USERINFO, userInfo);
    }
  }
 
  showHideWidgetsPanel(event){
    this.showAvailableWidgets = event;
    if(event){
      this.unusedWidgetsPanelOpts.expanded = true
    }
  }
  
  getWidgetIconName(reportType: string): string{
    switch(reportType){
      case 'DR':
        return 'bookmark';
      case 'PA':
        return 'document';
      case 'P':
        return 'document';
      case 'PC':
        return 'pie-chart';
      case 'DC':
        return 'doughnut-chart';
      case 'LC':
        return 'line-chart';
      case 'BC':
        return 'bar-chart';
      case 'AC':
        return 'area-chart';
      case 'L':
        return 'list';        
      default:
        return '';
    }
  }
  drop(event: CdkDragDrop<IoptionsPsContainerWidgetCard[]>) {
    if (event.previousContainer === event.container) {
      moveItemInArray(this.userWidgets, event.previousIndex, event.currentIndex);
    } else {
      transferArrayItem(event.previousContainer.data, event.container.data, event.previousIndex, event.currentIndex);
    }
    this.resizeWidgetChart()
    this.saveDashboardPersonalization();
  }  
 
  ngAfterViewInit() {
   if(this.currentLayout === 'grid'){
     this.removePlaceHolder();
   }
  }

  dropListEnterPredicate = (drag: CdkDrag, drop: CdkDropList) => {
    if (drop == this.placeholder) return true;
    if(drag.data){
     const widgetIndex = this.unAddedUserWidgets.findIndex(widget => widget.widgetCode === drag.data.widgetCode);
     return widgetIndex != -1 ? true : false
    }
  }
  
  dragMoved(e: CdkDragMove) {
    let point = this.getPointerPositionOnPage(e.event);
    this.autoScroll(point.x,point.y)
    this.listGroup._items.forEach((dropList) => {
    if (__isInsideDropListClientRect(dropList, point.x, point.y) && e.source.dropContainer != dropList) {
     if(this.activeContainer != dropList){
      this.dropListEntered(e.source,dropList)
     }
     return;
    }
    });
  }

  autoScroll(pointX,pointY){
    const container = this.container.nativeElement;
    const rect = container.getBoundingClientRect();
    const scrollSpeed = 10;

    clearInterval(this.autoScrollInterval);

    if (pointY < rect.top + 20) {
      this.autoScrollInterval = setInterval(() => {
        container.scrollBy(0, -scrollSpeed);
      }, 16);
    } else if (pointY > rect.bottom - 20) {
      this.autoScrollInterval = setInterval(() => {
        container.scrollBy(0, scrollSpeed);
      }, 16);
    }
    else if (pointX < rect.left + 20) {
      this.autoScrollInterval = setInterval(() => {
        container.scrollBy(-scrollSpeed, 0);
      }, 16);
    } else if (pointX > rect.right - 20) {
      this.autoScrollInterval = setInterval(() => {
        container.scrollBy(scrollSpeed, 0);
      }, 16);
    }
  }
  dropListEntered(drag: CdkDrag, dropList: CdkDropList) {
    const sourceElement: CdkDropList = drag.dropContainer;
    const dropElement: CdkDropList = dropList;  
    this.activeContainer = dropList

    if (!sourceElement || !dropElement) {
      console.warn('Source or Drop element is not defined.');
      return;
    }
    if (dropElement === this.placeholder) {
      return; // Exit if the placeholder is the drop container
    }
    const phElement = this.placeholder?.element?.nativeElement;
    const sourceNativeElement = sourceElement.element.nativeElement;
    const dropNativeElement = dropElement.element.nativeElement;
  
    if (!phElement || !sourceNativeElement || !dropNativeElement) {
      console.warn('One of the required elements (placeholder, source, or drop) is missing.');
      return;
    }
  
    const parentChildren = Array.from(dropNativeElement.parentElement?.children || []);
    const dragIndex = parentChildren.indexOf(this.source ? phElement :sourceNativeElement);
    const dropIndex = parentChildren.indexOf(dropNativeElement);
  
    if (!this.source) {
      // Initialize the source element and set placeholder dimensions
      this.sourceIndex = dragIndex;
      this.source = sourceElement;
  
      phElement.style.width = `${sourceNativeElement.clientWidth}px`;
      phElement.style.height = `${sourceNativeElement.clientHeight}px`;
  
      // Safely remove the source element
      sourceNativeElement.parentElement?.removeChild(sourceNativeElement);
    }
  
    // Update target index for drop
    this.targetIndex = dropIndex !== -1 ? dropIndex : null;
    phElement.style.display = ''
    // Insert placeholder into the correct position
    dropNativeElement.parentElement?.insertBefore(
      phElement,
      dropIndex > dragIndex ? dropNativeElement.nextSibling : dropNativeElement
    ); 
    // Allow placeholder to accept drag reference
    if (this.placeholder._dropListRef && drag._dragRef) {
      this.placeholder._dropListRef.enter(
        drag._dragRef,
        sourceNativeElement.offsetLeft,
        sourceNativeElement.offsetTop
      );
    } else {
      console.warn('Placeholder DropListRef or DragRef is not initialized.');
    }
  }
 
  dropListDropped(event: CdkDragDrop<number[]>) {
   if(this.activeContainer){
    let phElement = this.placeholder.element.nativeElement;
    let parent = phElement.parentElement;
    phElement.style.display = 'none';
    parent.removeChild(phElement);
    parent.appendChild(phElement);
    parent.insertBefore(this.source.element.nativeElement,parent.children[this.sourceIndex]);
   }
   if (this.sourceIndex !== null && this.targetIndex !== null && this.sourceIndex !== this.targetIndex) {
      moveItemInArray(this.userWidgets, this.sourceIndex, this.targetIndex);
   }
   else if (event.previousContainer !== event.container) {
    const parentChildren = Array.from(event.container.element.nativeElement.parentElement?.children || []);
    const dropIndex = parentChildren.indexOf(event.container.element.nativeElement);
    transferArrayItem(event.previousContainer.data, event.container.data, event.previousIndex, dropIndex);
    this.cdkDropListConnectedTo = this.generateCdkDropListConnectedTo()
   }
   this.resizeWidgetChart();
   this.resetState();
  this.saveDashboardPersonalization();
  }

  private resetState() {
    this.sourceIndex = null;
    this.targetIndex = null;
    this.source = null;
    this.activeContainer = null;
    clearInterval(this.autoScrollInterval);
  }
   
  removePlaceHolder(){
    let phElement = this.placeholder.element.nativeElement;
    phElement.style.display = 'none';
    phElement.parentElement.removeChild(phElement);
  }

  getPointerPositionOnPage(event: MouseEvent | TouchEvent) {
    const point = __isTouchEvent(event)? event.touches[0] || event.changedTouches[0] : event;
    const scrollPosition = this.viewportRuler.getViewportScrollPosition();

    return {
      x: point.pageX - scrollPosition.left,
      y: point.pageY - scrollPosition.top,
    };
  }
}


function __isTouchEvent(event: MouseEvent | TouchEvent): event is TouchEvent {
  return event.type.startsWith('touch');
}

function __isInsideDropListClientRect(dropList: CdkDropList, x: number, y: number) {
  const { top, bottom, left, right } =
    dropList.element.nativeElement.getBoundingClientRect();
  return y >= top && y <= bottom && x >= left && x <= right;
}


